.main{
    width: 100%;
    height: 100%;
   
   
}

.container{
    font-size: 22px;
    width: 450px;
    height:650px;
    margin: 0 auto;
    border-radius: 15px;
   margin: 0 auto;
    display: flex;
    flex-direction: column;
  
   top:50px;
   position: relative;
overflow: hidden;
font-family: 'Open Sans Condensed', sans-serif;
border:1px solid #fff;
}

*{
    margin: 0;
    padding: 0;
}

.search{
    width: 85%;
    height: 4rem;
    margin:0 auto;
    margin-top: -15px;
    border-radius: 15px;
    padding: 20px 20px 0px 50px;
    text-transform: uppercase;
outline: none;
border: none;
box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.7);
background-color: rgba(255, 255, 255, 0.863);
border:1px solid #242323;

}

.temperature{
    background-color:rgba(238, 229, 229, 0.438) ;
    color: #242323;
    height: 15%;
    width: 30%;
    border-radius: 15px;
  
    display: flex;
    text-align: center;
    justify-content: space-around;
    text-shadow: rgba(0, 0, 0, 0.493);
    box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.7),inset 5px 5px 10px #cac8c8c5;
  padding: 15%;
  padding-top: 10%;
    font-size: 250%;
    margin: 0 auto;
    border:1px solid #fff;
}

.weather-data{
    display: flex;
    flex-direction: column;
  
   
    justify-content: space-between;
    text-align: center;
    margin: 0 auto;
    margin-top: 50px;
    height:50%;
    width: 80%;
}
.moon{
    bottom: 50px;
    right: 10px;
    position: absolute;
    height: 50px;
    width: 50px;
    border: rgba(255, 255, 255, 0.863);
    background-color: rgba(238, 229, 229, 0.438);
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    box-shadow: 5px 5px 18px rgb(66, 65, 65), inset 2px 2px  5px #fff;
    margin-top: 5%;
}

.moon i{
    font-size: 30px;
    color: #e2e61f;
    cursor: pointer ;
}

.moon i:hover{
transform: scale(1.2);
}


@media only screen and (max-width: 360px) {
    .container{
        width:260px;
        height:450px;
        margin:  0 14%;
    }
  }
  @media only screen and (max-height: 568px) {
    .container{
        
        margin:  0 10%;
    }
  }

  @media only screen and (max-width: 768px)  {
    .container{
        width:260px;
        height:450px;
        margin:  0 14.5%;
    }
  }
  
  @media only screen and (min-width: 768px) and (max-height:1024px)  {
    .container{
        width:450px;
        height:650px;
     
    }
  }